.loginContainer {
  position: absolute;
  top: 120px;
  left: 50%;

  width: 348px;
  margin-left: -207px;
  padding: 35px 33px 42px;

  text-align: center;
}

.title {
  margin: 0 0 40px !important;

  font-family: 'Montserrat', sans-serif !important;
  font-size: 26px !important;
  font-weight: 600 !important;
  font-style: normal !important;
}
.buttonSubmit {
  cursor: pointer;

  padding: 10px;

  font-family: Montserrat, sans-serif;
  font-weight: 300;
  color: rgb(255, 255, 255);
  text-transform: uppercase;

  background-color: rgb(25, 118, 210);
  border: none;
  border-radius: 30px;
}

.container {
  display: flex;
  /*display: flex;*/
  flex-direction: column;
  align-items: center;

  width: 80%;
  height: 100%;
  margin-top: 60px;
}

.registration {
  width: 528px;
  height: 500px;
  margin: 20px;
  padding: 23px;

  background: #ffffff;
  border-radius: 2px;
  box-shadow: 1px 1px 2px rgb(0 0 0 / 10%), -1px -1px 2px rgb(0 0 0 / 10%);
}

.from_registration {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input_registration {
  width: 100%;
  margin-bottom: 24px;
}

.button_sing_up {
  width: 100%;

  background: #366eff;
  border-radius: 30px;
  box-shadow: 0 4px 18px rgba(54, 110, 255, 0.35), inset 0 1px 0 rgba(255, 255, 255, 0.3);
}
.error {
  font-family: 'Montserrat', sans-serif !important;
  font-weight: 600 !important;
  color: #b31232;
}
