.profile {
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;

  width: 413px;
  margin-top: 60px;
  padding: 27px 0 36px 0;
}

.text {
  font-size: 22px;
  font-weight: 600;
  line-height: 27px;
}

.email {
  margin-bottom: 29px;

  font-size: 14px;
  font-weight: 400;
  line-height: 24px;

  opacity: 0.5;
}

.editableSpan {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 347px;
  height: 78px;

  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
}
